import React from 'react';
import Socials from './Socials';
import { useSpring, animated } from 'react-spring';
import './Home.css';

export default function Home() {
  const h1 = useSpring({
    opacity: 1,
    transform: 'translate(0px,0px)',
    from: { opacity: 0, transform: 'translate(0px, 20px)' },
    delay: 100,
    config: { duration: 200 }
  });
  const title = useSpring({
    opacity: 1,
    from: { opacity: 0 },
    delay: 800,
    config: { duration: 300 }
  });
  const buttons = useSpring({
    opacity: 1,
    from: { opacity: 0 },
    delay: 1400,
    config: { duration: 300 }
  });
  return (
    <div className="Home">
      <div className="Content">
        <animated.h1 style={h1}>Almir Mujic</animated.h1>
        <animated.span style={title}>
          Front-End Developer | UX Designer
        </animated.span>
        <animated.div style={buttons} className="btn-container">
          <Socials />
        </animated.div>
      </div>
    </div>
  );
}
